
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}
.container {
  max-width: 75rem;
  margin: 0 auto;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-flow: row wrap;
          flex-flow: row wrap;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
}
.container > * {
  -webkit-box-flex: 0;
      -ms-flex: 0 0 100%;
          flex: 0 0 100%;
}
@media (min-width: 744px) {
.container > * {
    -webkit-box-flex: 0;
        -ms-flex: 0 0 33.33%;
            flex: 0 0 33.33%;
}
}
.component {
  -webkit-box-shadow: 0px 0px 20px rgba(266, 33, 33, .2);
          box-shadow: 0px 0px 20px rgba(266, 33, 33, .2);
  margin: 2rem;
  border-radius: 0.4rem;
}
.component__header {
  padding: 1rem 0;
  border-bottom: 1px solid #F8F8F8;
  color: #E7AFAF;
}
.component__block {
  padding: 2rem;
}



